/**
 * second_format
 * @param {*} second 
 */

export default (second) => {
    let day = Math.floor(second / (24 * 3600)); // Math.floor()
    let hour = Math.floor((second - day * 24 * 3600) / 3600);
    let minute = Math.floor((second - day * 24 * 3600 - hour * 3600) / 60);
    second = second - day * 24 * 3600 - hour * 3600 - minute * 60;

    let output = '';
    if (day) output += day + "";
    if (hour) output += hour + "";
    if (minute) output += minute + "";
    if (second) output += second + "";
    return output;
}

